.schedule-container {
    display: grid;
    width:600px;
    grid-template-columns: 1fr 4fr;
    align-items: center;
    justify-items: center;
    
    
}
.date-box {
   display: grid;
   padding: 5px;
   justify-items: center;
   height: 100%;
   width: 100%;
  
}

.color-box {
    color:white;
  
}

.color-box h1 h2{
    margin: 0%;
    padding: 0%;
}

.details {
    padding: 2%;
    height: 100%;
    width: 100%;
}


